.social-share-modal {
  a.icon-share {
    position: fixed;
    width: 48px;
    height: 48px;
    right: 0;
    bottom: 57px;
    z-index: 100;
    background: $pear;
    text-decoration: none;
    color: $white;

    &:before {
      position: absolute;
      font-size: 32px;
      left: 50%;
      top: 50%;
      transform: translate(-50%,-50%);
    }

    @media (min-width: $screen-sm-min) {
      bottom: 0;
    }

    @media (min-width: $screen-md-min) {
      display: none;
    }
  }

  .modal-dialog {
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;

    .social-share-link {
      display: inline-block;
    }

    .social-share-embed {
      text-align: left;
      background: $stone;
      padding: 1px 10px 10px;

      h6 {
        color: $white;
      }
    }

    .wp-embed-share-input {
      width: 100%;
      border: none;
      height: 28px;
      margin: 0 0 10px;
      padding: 0 5px;
      font: 400 14px/1.5 $font-family-sans-serif;
      resize: none;
      cursor: text;
    }

    textarea.wp-embed-share-input {
      height: 74px;
    }

    .wp-embed-share-description {
      margin: 0;
      font-size: 14px;
      line-height: 1;
      font-style: italic;
      color: $pearl;
    }
  }

  @media screen and (orientation:portrait) {
    body:not(.single-data-viz):not(.post-type-archive-data) & {
      .modal-dialog {
        width: 100px;
        margin-top: -240px;
        margin-left: -50px;
      }
    }
  }

  @media screen and (orientation:landscape) {
    .modal-dialog {
      width: 480px;
      margin-top: -50px;
      margin-left: -240px;
    }
  }

  .single-data-viz &, .post-type-archive-data & {
    .modal-dialog {
      max-width: 480px;
      margin-top: -175px;
      margin-left: -240px;

      @media (max-width: 480px) {
        width: 100%;
        margin-left: -50%;
      }
    }
  }
}

.social-share-buttons {
  display: none;

  @media (min-width: $screen-md-min) {
    display: inline-block;
    left: 0;
    top: 30%;
    z-index: 100;
    background: $white70;

    .count {
      font-family: $font-family-sans-serif;
      font-weight: 300;
      font-size: 19px;
      text-align: center;
      width: 100%;
      line-height: 1.2;

      .num {
        display: block;
        font-size: 28px;
        font-family: 'Open Sans Condensed';
      }
    }
  }
}

.social-share-link {
  display: block;
  text-indent: -100em;
  overflow: hidden;
  width: 100px;
  height: 100px;
  position: relative;
  text-decoration: none;
  margin: 16px auto;

  &:before {
    color: $text;
    text-indent: 0;
    position: absolute;
    font-size: 48px;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
  }

  @media screen and (max-width: $screen-sm-max) and (orientation: landscape) {
    display: inline-block;
    margin: 0 8px;
  }

  @media (min-width: $screen-md-min) {
    margin: 0;
    width: 40px;
    height: 40px;
    display: inline-block;

    &:before {
      font-size: 32px;
    }
  }

  .single-data-viz &, .post-type-archive-data & {
    margin: 0;
    width: 60px;
    height: 60px;

    &:before {
      font-size: 32px;
    }
  }

  &.icon-facebook {
    // background: #3b5998;
    // color: #3b5998;
    border: 1px solid $light-grey;
    border-radius: 50%;
    margin-left: 10px;
    color: #3B5998;

    &:before {
      color: #3B5998;
    }

    &:hover {
      border: 1px solid #3B5998;
    }
  }

  &.icon-twitter {
    // background: #00aced;
    border: 1px solid $light-grey;
    border-radius: 50%;
    margin-left: 10px;

    &:before {
      color: #00aced;
    }

    &:hover {
      border: 1px solid #00aced;
    }
  }

  &.icon-linkedin {
    // background: #007bb6;
    border: 1px solid $light-grey;
    border-radius: 50%;
    margin-left: 10px;

    &:before {
      color: $grey;
    }

    &:hover {
      border: 1px solid $grey;
    }
  }

  &.icon-email {
    // background: $scarlet;
    border: 1px solid $light-grey;
    // padding:  8px 10px;
    border-radius: 50%;
    margin-left: 10px;

    &:before {
      color: $grey;
    }

    &:hover {
      border: 1px solid $grey;
    }
  }
}
.other-share {
  @extend .social-share-link;

  &.icon-download {
    color: $grey;

    &:hover {
      border: 1px solid $grey;
    }
  }

  &.icon-fax {
    // background: $pear;
    border: 1px solid $light-grey;
    border-radius: 50%;
    margin-left: 10px;

    &:before {
      color: $grey;
    }

    &:hover {
      border: 1px solid $grey;
    }
  }

  .single-data-viz &, .post-type-archive-data & {
    @media screen and (max-width: $screen-sm-max) {
      display: inline-block !important;
    }
  }
}



.content-box-container-social {
  .ctf.ctf-type-usertimeline {
    margin-top: -85px;
  }
  #ctf .ctf-header {
    border-bottom: none;
  }
  #ctf .ctf-item {
    width: 50%;
    display: inline-block;
    vertical-align: top;
    // border: 1px solid black;
    // margin: -20px;
  }

  #ctf .ctf-item:nth-child(1) {
     border-top: initial;
  }

  #ctf .ctf-item:nth-child(2) {
     border-top: initial;
  }

  #ctf .ctf-item:nth-child(1) {
     margin-top: 30px;
  }
  #ctf .ctf-item:nth-child(2) {
     margin-top: 30px;
  }

  #ctf .ctf-header a {
      text-decoration: none;
      float: right;
  }

  p.ctf-tweet-text {
    font-family: $font-family-sans-serif;
    font-size: 15px;
  }

}

.ctf-tweets {
  padding-top: 85px;
}

#ctf #ctf-more {
    display: block;
    border: 1px solid $text;
    background: #fff;
    color: $text;
    width: 20%;
    margin: 0 auto;
    padding: 12px 40px;
    font-family: Lato;
    position: relative;
    text-align: center;
    box-sizing: border-box;
    outline: none;
    -moz-border-radius: 0px;
    -webkit-border-radius: 0px;
    border-radius: 0px;
    text-decoration: none;
    transition: all 0.2s;
}

#ctf #ctf-more:hover {
    display: block;
    border: 1px solid black;
    background: #fff;
    color: $text;
    width: 20%;
    margin: 0 auto;
    padding: 12px 40px;
    font-family: Lato;
    position: relative;
    text-align: center;
    box-sizing: border-box;
    outline: none;
    -moz-border-radius: 0px;
    -webkit-border-radius: 0px;
    border-radius: 0px;
    text-decoration: none;
    transition: all 0.2s;
}




@media screen and (max-width:820px) {
  #ctf .ctf-item {
      width: 100%;
      display: inline-block;
      vertical-align: top;
  }
}
