.center-bar {
  position: absolute;
  top: 50%;
  transform: translate3d(0, -50%, 0);
  background: $plum-darker;
  width: 100%;
  color: $white;
  text-align: center;
  padding: 14px 0;

  h3 {
    margin: 0;
    color: $white;
    font-family: $font-family-serif;
  }
}

