.widget_ednc_columns {
  .three-across {
    @media (min-width: $screen-sm-min) {
      .label {
        position: absolute;
        top: 50%;
        transform: translate3d(0, -50%, 0);
        // background: $plum-darker;
        width: 100%;
        color: $white;
        text-align: center;
        padding: 14px 0;
        font-size: 1.2em;
        height: auto;
      }
    }
    @media (min-width: $screen-sm-min) and (max-width: $screen-sm-max) {
      font-size: 80%;

      .post-title {
        font-size: 18px;
      }

      .byline {
        font-size: 14px;
      }
    }
  }

  .edtalk, .map {
    position: relative;

    h3 {
      color: $stone;
      margin-top: .5em;
    }

    .author a {
      text-decoration: none;
    }

    .photo-overlay img {
      transition: transform .5s;
    }

    &:hover {
      h3 {
        color: $jet;
      }

      .photo-overlay img {
        transform: scale(1.05);
      }
    }
  }
}


.widget_events {
  padding-bottom: 0 !important;

  .map-background {
    background-image: url('../images/line-map-background.jpg');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center bottom;

    @media (min-width: $screen-sm-min) {
      background-position: center;
    }
  }

  .submit-event {
    margin-top: 2em;
    margin-bottom: 4em;

    @media (min-width: $screen-sm-min) {
      margin-bottom: 0;
    }

    @media (min-width: $screen-md-min) {
      margin-top: 0;
      margin-bottom: 0;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate3d(-50%, -100%, 0);
    }
  }
}

.tribe-events-adv-list-widget.tribe-events-adv-list-widget {
  padding-top: 0 !important;
  margin-bottom: 0;

  .list-date {
    display: none;
  }

  .list-info {
    h2.tribe-events-title {
      @include h3;
      margin-bottom: 0;

      a {
        display: block;
        color: $ash;
        text-decoration: none;
        border-bottom: 1px solid transparent;
        margin-bottom: 1px;
        transition: all .3s;

        &:hover {
          border-bottom: 1px solid $heather;
          color: $jet;
        }
      }
    }

    .tribe-events-location, .tribe-events-duration {
      font-family: $font-family-sans-serif;
      font-weight: 300;
      font-size: 16px;
      color: $gray;

      a {
        color: $gray;
        text-decoration: none;
      }

      .tribe-events-locality {
        &:before {
          content: "| ";
        }
      }
    }
  }

  .tribe-events-widget-link {
    display: none;
  }
}
