.block-recommended_block {
  @include block-3(20px);
  h3 {
    font-weight: 600 !important;
    font-size: 17px !important;
  }
  p {
    font-size: 14px !important;
  }
  a {
    text-decoration: none;
  }
}

.recommended-reading-right-block {
  border: 1px solid blue;
}


.pulloutcontent{
  float:right;
  clear:both;
  /* This can be a set width or percent, if you want a pullout that floats left or right or full full width */
  width: 30%;
  /* Add padding as you see fit */
  padding: 20px 20px;
}

.pulloutContainer:before {
  content: '' ;
  display:block;
  float: right;
  /* The height is essentially a "margin-top" to push the pullout Container down page. */
  height: 80px;
}

#recommended-article-right {
  // display: flex;
  // flex-direction: column;
  // border: 1px solid blue;
  hr {
    margin-top: 15px;
    margin-bottom: 15px;
    border: 0;
    border-top: 1px solid #eee;
  }
}

.pulloutcontent h4 {
  font-size: 16px;
  margin-top: 0.5em;
  margin-bottom: 0.5em;
}


.pulloutcontent h4.header {
    font-weight: 600;
    padding: 10px 0px;
    border-top: 2px solid rgba(0, 0, 0, 0.84);
    border-bottom: 2px solid rgba(0, 0, 0, 0.84);
}

.longformintro--content p, .longformintro--content h1,
.longformintro--content h2, .longformintro--content h3,
.longformintro--content h4, .longformintro--content h5,
.longformintro--content h6 {
  color: white;
  text-align: center;
}

